@import "../../styles/global";
@import "../../styles/vars";
@import "../../styles/mixins";

.title {
  padding-top: 12%;
  padding-bottom: 4%;

  @include lgScreenMedia {
    padding-top: 120px;
    padding-bottom: 3%;
  }

  @include smScreenMedia {
    padding-top: 78px;
    padding-bottom: 20px;
  }
}

.capital {
  text-transform: uppercase;
}

a {
  color: $color-light-blue;
  &:hover, &:active {
    text-decoration: none;
  }
}

ul {
  margin: 10px 0 10px 35px;

  @include lgScreenMedia {
    margin: 5px 0 5px 30px;
  }

  li {
    margin: 10px 0;

    @include lgScreenMedia {
      margin: 5px 0;
    }
  }
}

strong {
  font-weight: 500;
  @include lgScreenMedia {
    font-weight: 600;
  }
}

h2 {
  @include setFont(29px, 500);
    padding: 1.2% 0;

  @include lgScreenMedia {
    padding: 2% 0;
  }

  @include xxlScreenMedia {
    font-size: 25px;
  }

  @include xlScreenMedia {
    font-size: 22px;
  }

  @include smScreenMedia {
    font-size: 21px;
  }
}

h3 {
  @include setFont(26px, 500);
    padding: 1% 0;

  @include lgScreenMedia {
    padding: 0.7% 0;
  }

  @include xxlScreenMedia {
    font-size: 23px;
  }

  @include xlScreenMedia {
    font-size: 21px;
  }

  @include smScreenMedia {
    font-size: 19px;
  }
}

h4 {
  @include setFont(21px, 500);
    padding: 0.5% 0;

  @include lgScreenMedia {
    padding: 0.2% 0;
  }

  @include xxlScreenMedia {
    font-size: 20px;
  }

  @include xlScreenMedia {
    font-size: 19px;
  }

  @include smScreenMedia {
    font-size: 17px;
  }
}

h5 {
  @include setFont(20px, 500);
    padding: 0.3% 0;

  @include lgScreenMedia {
    padding: 0.1% 0;
  }

  @include xxlScreenMedia {
    font-size: 19px;
  }

  @include xlScreenMedia {
    font-size: 18px;
  }

  @include smScreenMedia {
    font-size: 16px;
  }
}

p {
  padding: 10px 0;

  @include lgScreenMedia {
    padding: 5px 0;
  }
}